.form {
  width: 320px;
}

.label {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
}

.register {
  width: 100%;
  height: 40px;
  font-weight: 600;
  color: #382b22;
  padding: 3px;
  background: #fff9e0;
  border: 2px solid #17a2b8;
  border-radius: 0.75em;
  transform-style: preserve-3d;
  transition: transform 150ms cubic-bezier(0, 0, 0.58, 1),
    background 150ms cubic-bezier(0, 0, 0.58, 1);
}
